import React, { Component } from 'react'
import { Link } from 'react-router-dom'
export default class Cards extends Component {
    state = {
        data: undefined
    }
    componentDidMount(){
        
        fetch('/wp-json/wp/v2/card')
        .then(res =>{
          return res.json();
        })
        .then(res => {
          this.setState({data: res})
        })
      
}
    render() {
        return (
            <div className="container">
                <div className="row">
{
    this.state.data !== undefined ? 
    
    this.state.data.map(item => {
        return <div key={item.id} className="col-md-4">
        <h2>{item.acf.title}</h2>
        <p>{item.acf.description}</p>
        <Link to={`/post/${item.id}`}><button className="btn btn-secondary" >View details &raquo;</button></Link>
      </div>
    })
    
    
    : null
}
                </div>
            </div>
        )
    }
}
