import React, { Component } from 'react'

export default class CardDetails extends Component {
    state = {
        data: undefined
    }
    createMarkup(data) { return {__html: data}; }
    componentDidMount(){
        fetch(`/wp-json/wp/v2/card?include=${this.props.match.params.id}`)
        .then(res =>{
          return res.json();
        })
        .then(res => {
          this.setState({data: res[0]})
        })
    }
    render() {
        return (
            <div>
                {
                    this.state.data !== undefined ? <div className="jumbotron">
                    <div className="container">
                            <h1 className="display-3">{this.state.data.acf.title}</h1>
                            <p>{this.state.data.acf.description}</p>
                            <div dangerouslySetInnerHTML={this.createMarkup(this.state.data.acf.blogpost)}></div>
                    </div>
                </div> : null
                }
            </div>
        )
    }
}
