import React, { Component } from 'react'

export default class Header extends Component {
    state = {
        data: undefined
    }
    componentDidMount(){
        
            fetch('/wp-json/wp/v2/banner')
            .then(res =>{
              return res.json();
            })
            .then(res => {
              this.setState({data: res[0]})
            })
          
    }
    render() {

        return (
            <div>
                {
                    this.state.data !== undefined ? <div className="jumbotron">
                    <div className="container">
                            <h1 className="display-3">{this.state.data.acf.title}</h1>
                            <p>{this.state.data.acf.description}</p>
                            <a href={this.state.data.acf.link} target='_blank' rel='noreferrer' ><button className="btn btn-primary btn-lg" >Learn more &raquo;</button></a>
                    </div>
                </div> : null
                }
                
            </div>
        )
    }
}
